import { Injectable, NgZone } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/observable/fromEvent';
import 'rxjs/add/operator/auditTime';

import { FRAME_DURATION, isDocumentAvailable } from '../util';

import { DOMService } from './dom.service';

/**
 * @hidden
 */
@Injectable()
export class ResizeService {
    private subscription: Subscription;

    constructor(private _dom: DOMService, private _zone: NgZone) { }

    public subscribe(callback: Function): void {
        if (!isDocumentAvailable()) { return; }

        this._zone.runOutsideAngular(() => {
            this.subscription = Observable
                                    .fromEvent(this._dom.getWindow(), "resize")
                                    .auditTime(FRAME_DURATION)
                                    .subscribe(() => this._zone.run(() => callback()));
        });
    }

    public unsubscribe(): void {
        if (!this.subscription) { return; }

        this.subscription.unsubscribe();
    }

    public isUnsubscribed(): boolean {
        return this.subscription && this.subscription.closed;
    }
}



